  swiperClick(i){
    // console.log('click', i);

    const placeIdx = i;

    if (placeIdx > 0) {
      this.activeLayers[placeIdx - 1].setMap(null);
    }

    if (placeIdx > 0 && placeIdx < 7) {
      this.activeLayers[placeIdx + 1].setMap(null);
    }

    this.map.panTo(this.places[placeIdx].latLng);
    this.activeLayers[placeIdx].setMap(this.map);
    
    this.tripSwiper.swiper.slideTo(placeIdx);

  }


  componentDidMount() {
    this.loadMap();



    // init setMap - 프랑스
    this.activeLayers[2].setMap(this.map);

    this.tripSwiper.swiper.slideTo(2);
  }

